  display: none;
}

@font-face {
  font-family: "thisco_Brown";
  src: url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-regular.woff2") format("woff2"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-regular.woff") format("woff"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/BrownTT-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 300 600;
}
@font-face {
  font-family: "thisco_Brown";
  src: url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-bold.woff2") format("woff2"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-bold.woff") format("woff"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/BrownTT-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700 900;
}
@font-face {
  font-family: "thisco_Brown";
  src: url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-light.woff2") format("woff2"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/lineto-brown-light.woff") format("woff"), url("https://thiscovery-public-assets.s3.eu-west-1.amazonaws.com/fonts/BrownTT-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 100 200;
}
/* define official THIS colours */
:root {
  --thisco-dark-grey: rgb(58 60 66 / 1);
  --thisco-light-grey: rgb(210 212 211 / 1);
  --thisco-red: rgb(221, 0, 49);
  --thisco-bright-blue: rgb(91, 194, 231);
  --thisco-pale-blue: rgb(143, 172, 217);
  --thisco-green: rgb(0, 187, 126);
  --thisco-pink: rgb(243, 165, 185);
  --thisco-aubergine: rgb(104 4 54 / 1);
  --thisco-yellow: rgb(255 216 90 / 1);
  --thisco-pale-pink: #fce9ed;
  --thisco-light-pink: #fad2dd;
}

/* 

mobile-first layout ?? might be fighting against Qualtrics breakpoints....  
Q breakpoints seem to be... [ <480px, <767px (some weird 770px ones?), <979px ]

2.1.0 - ejects Q stylesheet

*/
html {
  font-size: 14px;
}

body {
  min-height: 100vh;
  background-color: var(--thisco-light-grey);
}

.Skin {
  font-family: "thisco_Brown", "Brown-Regular", Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: rgba(0, 0, 0, 0.95);
  font-weight: 100;
  font-size: 18px;
  line-height: 1.4;
  background-color: white;
  /* basic resets */
  /* resets */
  /* end resets */
}
@media (min-width: 481px) {
  .Skin {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .Skin {
    font-size: 20px;
  }
}
.Skin h1,
.Skin h2,
.Skin h3,
.Skin h4 {
  line-height: 1.2;
  margin-top: 1em;
  margin-bottom: 1em;
  font-weight: 400;
}
.Skin h1 {
  font-size: 2.2em;
  font-weight: 900;
}
.Skin h2 {
  font-size: 1.6em;
  font-weight: 900;
}
.Skin h3 {
  font-size: 1.4em;
}
.Skin h4 {
  font-size: 1.2em;
}
.Skin a,
.Skin a:hover,
.Skin a:visited,
.Skin a:focus {
  color: inherit;
  text-decoration: none;
}
.Skin img,
.Skin iframe {
  max-width: 100%;
}
.Skin a,
.Skin a:hover,
.Skin a:visited,
.Skin a:focus,
.Skin a:active {
  color: var(--thisco-red);
  transition: all 0.15s;
  text-decoration: none;
}
.Skin a:hover,
.Skin a:focus {
  text-decoration: underline;
}
.Skin a:focus {
  outline: 1px solid var(--thisco-pink);
}
.Skin .SkinInner {
  padding-top: 7rem;
}
.Skin .SkinInner ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list),
.Skin .SkinInner ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) {
  padding-left: 2.8em;
}
.Skin .SkinInner ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li,
.Skin .SkinInner ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li {
  display: list-item;
  position: relative;
  margin-bottom: 0.4em;
}
.Skin .SkinInner ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) {
  counter-reset: count 0;
}
.Skin .SkinInner ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li {
  counter-increment: count 1;
}
.Skin .SkinInner ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li:before {
  content: counter(count);
  color: var(--thisco-red);
  position: absolute;
  left: -2ch;
}
.Skin .SkinInner ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li:before {
  content: " ";
  color: var(--thisco-red);
  position: absolute;
  display: inline-block;
  height: 0.7ch;
  width: 0.7ch;
  border-radius: 50%;
  background-color: var(--thisco-red);
  top: 0.8ch;
  left: -2ch;
}
.Skin .SkinInner dl {
  display: grid;
  grid-template-columns: fit-content(20%) auto;
  grid-gap: 0.4em;
  padding-left: 2.8em;
}
.Skin .SkinInner dl > small {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: subgrid;
}
.Skin .SkinInner dl dt {
  font-weight: 400;
}
@media (min-width: 768px) {
  .Skin .SkinInner {
    padding-top: 8.5rem;
  }
}
.Skin .SkinInner img,
.Skin .SkinInner iframe,
.Skin .SkinInner embed {
  max-width: 90%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.Skin .SkinInner iframe,
.Skin .SkinInner embed {
  aspect-ratio: 16/9;
  height: auto !important;
}
.Skin #HeaderContainer {
  background-color: var(--thisco-light-grey);
  position: fixed;
  left: 0px;
  width: 100vw;
  z-index: 100;
  top: 0px;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
  padding: 0px 1rem;
}
