.thisco-survey {
  font-family: "thisco_Brown", "Brown-Regular", Arial, "Helvetica Neue", Helvetica, sans-serif;
  color: rgba(0, 0, 0, 0.95);
  font-weight: 100;
  font-size: 18px;
  line-height: 1.4;
  background-color: white;
  /* resets */
  width: 100vw;
  height: 100vh;
  display: flex;
  padding: 2rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.thisco-survey a,
.thisco-survey a:hover,
.thisco-survey a:visited,
.thisco-survey a:focus,
.thisco-survey a:active {
  color: var(--thisco-red);
  transition: all 0.15s;
  text-decoration: none;
}
.thisco-survey a:hover,
.thisco-survey a:focus {
  text-decoration: underline;
}
.thisco-survey a:focus {
  outline: 1px solid var(--thisco-pink);
}
.thisco-survey ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list),
.thisco-survey ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) {
  padding-left: 2.8em;
}
.thisco-survey ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li,
.thisco-survey ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li {
  display: list-item;
  position: relative;
  margin-bottom: 0.4em;
}
.thisco-survey ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) {
  counter-reset: count 0;
}
.thisco-survey ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li {
  counter-increment: count 1;
}
.thisco-survey ol:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li:before {
  content: counter(count);
  color: var(--thisco-red);
  position: absolute;
  left: -2ch;
}
.thisco-survey ul:not(.ChoiceStructure):not(.ChoiceGroupStructure):not(.thisco-grid-list) > li:before {
  content: " ";
  color: var(--thisco-red);
  position: absolute;
  display: inline-block;
  height: 0.7ch;
  width: 0.7ch;
  border-radius: 50%;
  background-color: var(--thisco-red);
  top: 0.8ch;
  left: -2ch;
}
.thisco-survey dl {
  display: grid;
  grid-template-columns: fit-content(20%) auto;
  grid-gap: 0.4em;
  padding-left: 2.8em;
}
.thisco-survey dl > small {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: subgrid;
}
.thisco-survey dl dt {
  font-weight: 400;
}
.thisco-survey h1,
.thisco-survey h2,
.thisco-survey h3,
.thisco-survey h4 {
  line-height: 1.2;
  margin-top: 1em;
  margin-bottom: 1em;
  font-weight: 400;
}
.thisco-survey h1 {
  font-size: 2.2em;
  font-weight: 900;
}
.thisco-survey h2 {
  font-size: 1.6em;
  font-weight: 900;
}
.thisco-survey h3 {
  font-size: 1.4em;
}
.thisco-survey h4 {
  font-size: 1.2em;
}
@media (min-width: 481px) {
  .thisco-survey {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  .thisco-survey {
    font-size: 20px;
  }
}
.thisco-survey > * {
  flex-basis: 100%;
  margin-bottom: 1.6em;
}
.thisco-survey img {
  max-width: 90%;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.thisco-survey iframe,
.thisco-survey embed {
  min-height: 10vw;
  aspect-ratio: 16/9;
  height: auto !important;
  padding-left: 2rem;
  padding-right: 2rem;
}

@media (min-width: 768px) {
  .survey-container {
    max-width: 768px;
    margin: auto;
  }
}
.survey-container .thisco-survey {
  width: 100%;
  height: auto;
}

