dialog {
  visibility: hidden;
}

.dialog-container,
.dialog-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.dialog-container {
  z-index: 2;
  display: flex;
  padding: 2rem;
}

.dialog-container[aria-hidden=true] {
  display: none;
}

.dialog-overlay {
  background-color: rgba(43, 46, 56, 0.9);
}

.dialog-content {
  margin: auto;
  z-index: 2;
  position: relative;
  background-color: white;
  border-radius: 25px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
